import React, { useCallback, useState } from "react";
import ReactPlayer from "react-player";
import styled from "styled-components";
import { useUserVideos } from "../../common/hooks/useUserVideos";
import { config } from "../../config/api";

const VideoManagement = ({ className }) => {
  const { data } = useUserVideos();
  const [url, seturl] = useState(data?.[0]?.video && `${config.apiResource}${data?.[0]?.video}`);
  const handleVideo = useCallback((url) => {
    url && seturl(`${config.apiResource}${url}`);
  }, []);
  // console.log(data);
  return (
    <section className={`${className} user__management__video col-12 col-lg-9`}>
      <header className="fs__title fw-bolder mb-2">Video của bạn</header>
      {url && (
        <div className="step__form__video">
          <ReactPlayer
            width="100%"
            height="auto"
            url={url}
            controls={true}
            muted={true}
            playing={true}
            style={{ width: "100%", height: "100%" }}
          />
        </div>
      )}
      {data?.map((video, i) => {
        const { name_i, number_order } = video || {};
        return (
          <div className="card" key={i} onClick={() => handleVideo(video?.video)}>
            <div className="card__video">
              <img
                src="https://www.sohoceravietnam.com/public/img/314a50ada9025a5c0313.jpg"
                alt=""
              />
            </div>
            <div className="card__body">
              <p className="card__body__title fs__text fw-bolder">
                {name_i} {number_order}
              </p>
              <p className="card__body__date fs__smallest">{video?.desc_i}</p>
            </div>
          </div>
        );
      })}
    </section>
  );
};

export default styled(VideoManagement)`
  &.user__management {
    &__video {
      /* width: 60%; */
      .card {
        cursor: pointer;
        margin-top: 20px;
        display: flex;
        flex-direction: row;
        background-color: ${(props) => props.theme.colorCard};
        border-radius: 16px;
        padding: 10px 20px 10px 10px;
        &__video {
          width: 150px;
          height: 100px;
          margin-right: 20px;
          img {
            border-radius: 16px;
            width: auto;
            height: 100%;
            object-fit: cover;
          }
        }
        &__body {
          display: flex;
          flex-direction: column;
          p:first-child {
            margin-bottom: 5px;
          }
        }
      }
    }
  }
`;
